#container {
	width: 1280px;
}
.overlay-video-cta h3 {
	color: #ffffff;
	font-size: 18pt;
	margin: 15px 0 0;
}
.overlay-video-cta > a {
	background: #ec1d29;
	color: #ffffff;
	font-size: 16pt;
	border-radius: 4px;
	display: block;
	margin: 15px auto;
	width: 140px;
	text-decoration: none;
	height: 50px;
	line-height: 50px;
}
.overlay-video-cta p {
	color: #ffffff;
	font-size: 12pt;
}
.overlay-video-cta p a {
	color: #ffffff;
	text-decoration: none;
}
.overlay-video-cta p a:hover {
	text-decoration: underline;
}
